* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Nunito", sans-serif;
}
:root{
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
}


body{
  color: var(--secondary);
}

  
.logregbtn {
    border: none;
    outline: none;
    width: 100%;
    padding: 15px 0;
    color: #fff;
    font-size: 16px;
    letter-spacing: 1px;
    background: var(--primary);
    cursor: pointer;
}
.logout {
    background:var(--primary);
    border: none;
    outline: none;
    border-radius: 7px;
    color: #fff;
    font-size: 20px;
    padding: 5px;
    margin-left: 60px;
    margin-bottom: 20px;
    height: 35px;
}
.logout:hover {
    border-radius: 12px;
    background: #e08079;
}
  
.login {
    width: 100%;
    min-height: 100vh;
    padding: 0 20px;
    background: #e9e9e9;
    display: flex;
}
  
.login .loginContainer {
    padding: 60px;
    margin: auto;
    width: 100%;
    max-width: 520px;
    min-height: 600px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background:  rgba(93, 199, 97,0.7);
    box-shadow: 0px 6px 50px rgba(217, 229, 255, 0.7);
    border-radius: 20px;

}
  
.login .loginContainer label {
    font-weight: 500;
    font-size: 30px;
    line-height: 23px;
    margin: 14px 0;
    display: block;
    text-align: center;
}
  
.login .loginContainer input {
    height: 45px;
    width: 100%;
    font-size: 14px;
    line-height: 16px;
    border: 2px solid #E9F2FF;
    border-radius: 5px;
    padding-left: 25px;
    background-color: rgba(255, 255, 255, 0.7);
}
  
.login .loginContainer .btnContainer {
    width: 100%;
    padding: 24px 0;
}
  
.login .loginContainer .btnContainer p {
    margin: 14px 0 0 0;
    text-align: right;
    color: #fff;
}
  
.login .loginContainer .btnContainer p span {
    color: yellow;
    font-weight: 500;
    letter-spacing: 0.5px;
    margin-left: 5px;
    cursor: pointer;
    transition: all 400ms ease-in-out;
}
  
.login .loginContainer .btnContainer p span:hover {
    color: red;
}
  
.login .loginContainer .errorMsg {
    color: red;
    font-size: 16px;
}